.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/* Smooth scrolling */
html {
  scroll-behavior: smooth;
}

/* Section spacing */
section {
  padding: 80px 20px;
  max-width: 1200px;
  margin: 0 auto;
  width: 100%;
}

/* Responsive design */
@media (max-width: 768px) {
  section {
    padding: 60px 20px;
  }
}
