.about {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: #fff;
  padding: 100px 20px;
}

.about-container {
  max-width: 1200px;
  margin: 0 auto;
}

.about-content {
  text-align: center;
}

.about-content .section-title {
  color: #fff;
  margin-bottom: 2rem;
}

.about-text {
  max-width: 800px;
  margin: 0 auto;
}

.about-text p {
  font-size: 1.1rem;
  line-height: 1.8;
  margin-bottom: 1.5rem;
  color: rgba(255, 255, 255, 0.95);
}

.about-stats {
  display: flex;
  justify-content: center;
  gap: 4rem;
  margin-top: 3rem;
  flex-wrap: wrap;
}

.stat {
  text-align: center;
}

.stat h3 {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
  color: #fff;
}

.stat p {
  font-size: 1rem;
  color: rgba(255, 255, 255, 0.9);
  text-transform: uppercase;
  letter-spacing: 1px;
  margin: 0;
}

@media (max-width: 768px) {
  .about-text p {
    font-size: 1rem;
  }

  .about-stats {
    gap: 2rem;
  }

  .stat h3 {
    font-size: 2rem;
  }
}
